/* jshint strict: true, browser:true, jquery: true */
/*jshint esversion: 6 */
/*jshint esversion: 9 */
// Module logius/style
// Inserts a link to the appropriate Logius style for the specification's maturity level.
// CONFIGURATION
//  - specStatus: the short code for the specification's maturity level or type (required)

import { createResourceHint, linkCSS, toKeyValuePairs } from "../core/utils.js";
import { pub, sub } from "../core/pubsubhub.js";
export const name = "logius/style";
function attachFixupScript(doc, version) {
  const script = doc.createElement("script");
  script.addEventListener(
    "load",
    () => {
      if (window.location.hash) {
        window.location.href = window.location.hash;
      }
    },
    { once: true }
  );
  script.src = `https://www.w3.org/scripts/TR/${version}/fixup.js`;
  doc.body.appendChild(script);
}

/**
 * Make a best effort to attach meta viewport at the top of the head.
 * Other plugins might subsequently push it down, but at least we start
 * at the right place. When ReSpec exports the HTML, it again moves the
 * meta viewport to the top of the head - so to make sure it's the first
 * thing the browser sees. See js/ui/save-html.js.
 */
function createMetaViewport() {
  const meta = document.createElement("meta");
  meta.name = "viewport";
  const contentProps = {
    width: "device-width",
    "initial-scale": "1",
    "shrink-to-fit": "no",
  };
  meta.content = toKeyValuePairs(contentProps).replace(/"/g, "");
  return meta;
}


function createStyle(css_name) {
  // Todo
  const link = document.createElement("link");
  link.rel = "stylesheet";
  link.href = `css/${css_name}.css`;
  //link.href = `https://gitlab.com/logius/tools/respec/style/${css_name}.css`;
  return link;
}

function createResourceHints() {
  // todo fix pathes 
  const resourceHints = [
    {
      hint: "preconnect", // for W3C styles and scripts.
      href: "https://www.w3.org",
    },
    {
      hint: "preload", // all specs need it, and we attach it on end-all.
      href: "https://www.w3.org/scripts/TR/2016/fixup.js",
      as: "script",
    },
    // {
    //   hint: "preconnect", // for Logius styles and scripts.
    //   href: "https://gitlab.com/logius/tools",
    // },
    {
      hint: "preload", // all logius specs import base.css.
      href: "cvs.css",
      //href: "https://gitlab.com/logius/tools/respec/style/base.css",
      as: "style",
    },
    {
      hint: "preload", // all Logius specs import base.css.
      href: "cvs.css",
      //href: "https://gitlab.com/logius/tools/respec/style/base.css",
      as: "style",
    },
    {
      hint: "preload", // all specs show the Logius logo.
      href: "https://www.logius.nl/themes/custom/logius/logo.svg",
      as: "image",
    },
  ]
    .map(createResourceHint)
    .reduce((frag, link) => {
      frag.appendChild(link);
      return frag;
    }, document.createDocumentFragment());
  return resourceHints;
}
// Collect elements for insertion (document fragment)
const elements = createResourceHints();

// add favicon for Logius
const favicon = document.createElement("link");
favicon.rel = "shortcut icon";
favicon.type = "image/x-icon";
favicon.href =
  "https://www.logius.nl/themes/custom/logius/favicon.ico";
document.head.prepend(favicon);

if (!document.head.querySelector("meta[name=viewport]")) {
  // Make meta viewport the first element in the head.
  elements.prepend(createMetaViewport());
}

document.head.prepend(elements);

export function run(conf) {
  if (!conf.specStatus) {
    const warn = "`respecConfig.specStatus` missing. Defaulting to 'base'.";
    conf.specStatus = "base";
    pub("warn", warn);
  }

  if (document.body.querySelector("figure.scalable")) {
    // Apply leaflet style if class scalable is present
    document.head.appendChild(createStyle("leaflet"));
    document.head.appendChild(createStyle("font-awesome"));
  }

  let styleFile = "";

  // Figure out which style file to use.
  switch (conf.specStatus.toUpperCase()) {
    // TODO
    case "ED":
      styleFile += "cvs.css";
      break;
    default:
      styleFile = "cvs.css";
  }

  if (!conf.noToc) {
    sub(
      "end-all",
      () => {
        attachFixupScript(document, "2016");
      },
      { once: true }
    );
  
  }
  // Todo
  //const finalStyleURL = `https://gitlab.com/logius/tools/respec/style/${styleFile}`;
  const finalStyleURL = `${styleFile}`;
  linkCSS(document, finalStyleURL);
  // Make sure the W3C stylesheet is the last stylesheet, as required by W3C Pub Rules.
}
