// @ts-check
// Module logius/l10n
// Looks at the lang attribute on the root element and uses it to manage the config.l10n object so
// that other parts of the system can localise their text
import { hyperHTML } from "../core/import-maps.js";
import { l10n } from "../core/l10n.js";
export const name = "logius/l10n";
const additions = {
  en: {
    status_at_publication:
    hyperHTML `This section describes the status of this document at the time of its publication. Other documents may supersede this document.`,
  },
  nl: {
    status_at_publication:
    hyperHTML `Deze paragraaf beschrijft de status van dit document ten tijde van publicatie. Het is mogelijk dat er actuelere versies van dit document bestaan.`,
  }
};

Object.keys(additions).forEach(key => {
  if (!l10n[key]) l10n[key] = {};
  Object.assign(l10n[key], additions[key]);
});
